<template>
    <v-app>
        <nav>
            <v-toolbar color="cyan" light>
                <v-toolbar-title @click="gotoMain" style="color: white; margin-left: 20px; cursor: pointer;">DK-Zone</v-toolbar-title>
                <v-spacer></v-spacer>
            </v-toolbar>
        </nav>
        <v-container>
            <v-layout style="margin: 50px 5px" justify-center>
                <v-flex xs12 sm10>
                    <v-card>
                        <v-card-title>
                            <div>
                                <h3 style="width:100%;">  ...</h3>
                            </div>
                        </v-card-title>
                        <v-form style="margin: 0 20%;">
                            <v-text-field label="" v-model="feed.title"></v-text-field>
                            <v-textarea label="" auto-grow=true; v-model="feed.text" style="margin-top:-15px;"></v-textarea>
                            <v-text-field label=" (  /  5)" v-model="feed.tag" style="margin-top:-15px;"></v-text-field>
                            [Beta - Version]   ,     .
                            <v-layout justify-center wrap>
                                <v-btn small outlined @click="gotoMain" style="width:180px; margin: 10px;">  </v-btn>
                                <v-btn small outlined @click="photofilemodal = true" style="width:180px; margin: 10px;">   </v-btn>
                                <v-btn small outlined @click="videofilemodal = true" style="width:180px; margin: 10px;">   </v-btn>
                                <v-btn small outlined disabled style="width:180px; margin: 10px;">   </v-btn>
                                <v-btn small outlined @click="Create" style="width:180px; margin: 10px;"> </v-btn>
                                <v-dialog v-model="photofilemodal" width="400">
                                        <v-card>
                                        <v-card-title>
                                            <h4>   </h4>
                                        </v-card-title>
                                        <v-card-text>
                                              ,     . (Beta - Version)
                                            <v-file-input label=" " v-model="files" show-size accept="image/*"></v-file-input>
                                        </v-card-text>
                                        <v-divider></v-divider>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="primary" text @click="FileUpload(1)"></v-btn>
                                            <v-btn color="primary" text @click="photofilemodal = false"></v-btn>
                                        </v-card-actions>
                                    </v-card>
                                </v-dialog>
                                <v-dialog v-model="videofilemodal" width="400">
                                        <v-card>
                                        <v-card-title>
                                            <h4>   </h4>
                                        </v-card-title>
                                        <v-card-text>
                                              ,     . (Beta - Version)
                                            <v-file-input label=" " v-model="files" show-size accept="video/*"></v-file-input>
                                        </v-card-text>
                                        <v-divider></v-divider>
                                        <v-card-actions>
                                            <v-spacer></v-spacer>
                                            <v-btn color="primary" text @click="FileUpload(2)"></v-btn>
                                            <v-btn color="primary" text @click="videofilemodal = false"></v-btn>
                                        </v-card-actions>
                                    </v-card>
                                </v-dialog>
                            </v-layout>
                        </v-form>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>
    </v-app>
</template>

<script>
export default {
    data(){
        return{
            feed: {
                url: "",
                title: "",
                text: "",
                tag: "",
            },
            files: null,
            photofilemodal: false,
            videofilemodal: false,
        }
    },
    methods: {
        CheckSession: function () {
            this.$http.get('/users/check')
            .then((res) => {
                if(res.data.islogin != true){
                    alert("     .");
                    this.$router.push('/');
                }
            })
            .catch(function (err) {
                alert(err);
            })
        },
        gotoMain: function(){
            if(confirm("     .    ?") == 1){
                this.$router.push('/lists');
            }
        },
        FileUpload: function(type) {
            let self = this;
            let filetype = type;
            let formData = new FormData();
            formData.append("files", this.files);
            this.$http.post("/feed/uploadfile", formData)
            .then((res) => {
                if(res.data.success == 1){
                    self.$http.post('/feed/write', {
                        url: res.data.route,
                        title: self.feed.title,
                        text: self.feed.text,
                        tag: self.feed.tag,
                        type: filetype
                    })
                    .then((res) => {
                        if(res.data.success == 1){
                            alert(res.data.message);
                            this.$router.push('/lists');
                        }
                        else{
                            alert(res.data.message);
                            self.files = null;
                        }
                    })
                }
                else{
                    alert(res.data.message);
                    self.files = null;
                }
            })
            .catch(function (err) {
                alert(err);
            });
        },
        Create: function () {
            let self = this;
            this.$http.post('/feed/write', {
                title: this.feed.title,
                text: this.feed.text,
                tag: this.feed.tag,
                type: 0
            })
            .then((res) => {
                if(res.data.success == 1){
                    alert(res.data.message);
                    this.$router.push('/lists');
                }
                else{
                    alert(res.data.message);
                    self.files = null;
                }
            })
            .catch(function (err){
                alert(err);
            });
        }
    },
    created() {
        this.CheckSession();
    }
}
</script>

<style scoped>
</style>